/* JS LOAD MORE */
.js-load{font-size:14px;height:40px;line-height:40px;font-weight:800;cursor:pointer;display:inline-block;text-indent:initial;position:relative;border-radius:8px;outline:0;padding:0 16px;transition:background .8s ease 0s;background:radial-gradient(circle,transparent 1%,transparent 1%) center center / 15000% transparent;border-width:1px;border-style:solid;border-color:#03ac0e;border-image:initial}
/* CSS LABEL */
.label-size-1,.label-size-2,.label-size-3,.label-size-4,.label-size-5{font-size:100%;opacity:10}
