/* CART */
.simpleCart_items td{padding:10px 10px!important;vertical-align:middle;}
.simpleCart_items .item-quantity input{width:20px;padding:10px;border:0}
.cart {float:left;display: inline-block;position: relative;cursor: pointer;}
.menu-cart{font-weight:600;text-transform:uppercase}
