.cart .item-decrement a,.cart .item-increment a{font-weight:600;font-size:14px;color:#f91b1b}
.cart a.cart-btn{position:absolute;top:-10px;right:0px;}
.cart-btn svg {width:20px;height:20px;vertical-align:-5px;fill: currentColor;outline:none}
.cart .cart-box table,.cart .cart-box th,.cart .cart-box td{border:none}
.cart .cart-box .item-thumb{padding:0!important;}
