/* SHOP CSS */
.button-home {font-size:13px;margin:0 20px auto;overflow-x: auto;overflow-y: hidden;white-space: nowrap;transition: all 0.5s;}
.keranjang-kosong {top: 42px!important;width: fit-content;margin-right: 10%;}
.fa-shopping-basket {font-size: 50px;color: #656565;margin-left:0!important;}.center{margin-left:0!important;}
.text-label{font-size:12px;text-align:center;bottom:0;width:100%;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;white-space:initial;left:-6px;position:relative;word-break:break-word;margin:0;overflow:hidden}
.icon-tag:hover {color:#42b549!important;}
.icon-tag{float:left;display:inline-block;width:10%;text-align:center;padding:12px 9px;margin-right:9.7px;color:#9e9e9e!important}
.bus {display: inline-block;padding: 21.5px;background-image: url(data:image/png;base64,);background-position: center;background-repeat: no-repeat;background-size: contain;margin:0;}
.bus img{width:46px;-webkit-transition:-webkit-transform .2s ease;-webkit-transition:transform .2s ease;transition:transform .2s ease;display:inline-block;vertical-align:middle}
.bus img:hover{webkit-transform:scale(1.1);-ms-transform:scale(1.1);transform:scale(1.1)}
.daftar-produk{position:relative;text-align:left;margin-bottom:20px}
.daftar-produk,.judul-produk-lainnya, .judul-review {color: #42b549;}
.daftar-produk {margin-bottom: 30px;}
.daftar-produk:after,.judul-produk-lainnya:after,.judul-review:after{z-index:1;content:"";width:150px;height:0;position:absolute;left:0;margin-top:-3px;border-bottom:3px solid #42b549}
.judul-produk-lainnya:after, .judul-review:after, .daftar-produk:after, .judul-produk-lainnya:after, .judul-review:after {background: #42b549;}
.daftar-produk h2:before {margin-right:6px;vertical-align:-7px;width:24px;height:24px;display:inline-block; transition:all .3s ease; background: url("data:image/svg+xml;base64,") no-repeat; content:''}
.daftar-produk h2 {font-size:14px!important;font-weight:600!important;color:rgba(0, 0, 0, 0.38)!important}
.daftar-produk h2, .judul-produk-lainnya h3,.judul-review h3{display:block;margin:0;text-transform:none;font-size:16px;border-bottom:3px solid #ededed;color:#333333;z-index:1;padding:6px 0;font-weight:normal}
.item_price, .info .simpleCart_total {font: bold 14px Open Sans, Arial, sans-serif;color: #ff5722;font-stretch:normal;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;}
.status-produk {position: absolute;bottom:115px;left:10px;}
.promo,.freeOngkir,.stok-habis{position:relative;padding:0 5px;margin-bottom:4px;border-radius:3px;color:#fff;font-size:12px;text-transform:uppercase;font-weight:700;height:24px;line-height:24px}
.promo {color: #03AC0E;background: #D6FFDE;}
.freeOngkir {color:rgb(3, 172, 14);background:rgb(214, 255, 222);font-weight:700;text-transform:none;font-size:10px;}
.stok-habis {color: #ffffff;background: #e25252;text-transform:none}
.totalCart {margin-right:20px;display: inline-block;vertical-align: middle;font-size: 10px;line-height:16px;
color:rgba(49,53,59,.44);font-weight:700;text-transform: uppercase;}
.totalCart .simpleCart_total {display:block;font-size:18px;color:#31353b}
.tombol-beli {margin:0;margin: 0 auto;position: fixed;left: 0;z-index: 99;border-radius: 16px 16px 0 0;right: 0;bottom: 0;box-shadow: 0 -2px 6px 0 rgba(49,53,59,.16);background: #fff;padding:20px;}
.rating-produk {margin: 0 0 5px;}
.rating:after{content:"Ulasan";padding:0 5px}
.skor:after {content:"-";padding:0 5px;}
.rating, .skor {font-size:14px;}
.fa-star, .fa-star-half-o {color:#FFC107;margin:0.7px;font-size:13px!important;}
.beli-lewat-chat {margin: 0 auto;max-width: 1220px;padding:0;}
.beli-lewat-chat a, .chat-wa {display: inline-block;margin-left:10px;vertical-align: middle;padding:13px 19px;border-radius:8px;font-weight:600;font-size:15px;background: #42b549;color: #fff;white-space: nowrap;}
.item_add {background:#ef430c!important;min-width:120px;}
.chat-sms {background:#fff!important;border:1px solid #ef430c!important;color:#ef430c!important;}
.beli-lewat-chat a i, .chat-wa i, .chat-sms i, .chat-phone i {margin-right:3px;}
.chat-wa i, .chat-phone i {font-size:20px;}
.chat-sms i {font-size: 15px;}
#admin {float:left;width:36%;display:inline-block;}
#admin img {width:34px;height:34px;margin-right:10px;border-radius:50%;}
#myIdBlog{display:none!important}
.info-admin, .follow-admin, .info-admin a, #admin a {background:transparent;display:inline-block;margin:0;padding:0;}
.info-admin a {vertical-align:middle;text-transform:uppercase;display:inline-block;font-size:12px;font-weight:700;letter-spacing:.1px;color:rgba(49,53,59,.88);}
.info-admin p {overflow:hidden;margin:0;padding:0;line-height: 14px;font-size: 10px;color: rgba(49,53,59,.68);white-space: nowrap;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp:2;max-width:200px;}
.follow-admin {margin-left:15px;vertical-align:top;}
.follow-admin a {margin: 5px 5px 5px 0!important;padding:6px 15px!important;border-radius:5px;font-weight:500;font-size:13px;background:#4fa149!important;color:#fff;}
/* CSS IMAGES */
.daffadev{font-size:14px;line-height:1.6}
.daffadev .wrap .title .kantup{display:block;color:rgba(0,0,0,.38);padding:0 20px;cursor:pointer;font-size:30px;font-weight:300;transition:.2s}
.daffadev .wrap .title .kantup:hover {color:#434343;width:auto;height:auto;}
.daffadev b,.daffadev strong{font-weight:700}
.daffadev{display:flex!important;position:relative;position:fixed!important;z-index:+1000;top:0;left:0;width:100%;height:100%;overflow-y:auto;padding:10px 0;visibility:hidden;opacity:0;background-color: rgba(0,0,0,0.5);}
.daffadev.open{visibility:visible;opacity:1;transition:.2s}
.daffadev a{text-decoration:none;padding:0;}
.daffadev .daffadev-wrap {padding:20px}
.daffadev .wrap{display:block;margin:auto auto;width:calc(100% - 20px);box-shadow:0 20px 50px rgba(0,0,0,.2);max-width:500px}
.daffadev .wrap .title{display:grid;width:100%;grid-template-columns:1fr auto;grid-column-gap:10px;margin:0;height:60px;line-height:60px;border-bottom:1px solid rgba(0,0,0,.06);font-size:100%;background:#fff;font-weight:600;border-radius:2px 2px 0 0}
.daffadev .wrap .title span.title-content{padding:0 20px;display:block;vertical-align:middle;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;color:#656565;font-size:110%}
.daffadev .wrap .content{display:block;overflow-y:auto;max-height:calc(100vh - 80px);background:#fff;border-radius:0 0 2px 2px}
.daffadev-wrap .caption {color:#333;font-size:89%;}
.daffadev .imgNotif a {line-height:0;}
.icon-promosi{display:inline-block;vertical-align:middle;width:20px;height:20px;margin-right:17px;background-position:center;background-repeat:no-repeat;background-size:18px 18px;background-image:url(../cdn.tokopedia.net/img/seller-dashboard/icon-smenu-topads.svg)}
.grid-wa{display:grid;grid-gap:10px}
.grid-wa.col2{grid-template-columns:1fr 1fr}
.dropdown-content .closeUcihaItachi, .search-content .closeUcihaItachi{position:fixed;z-index:-1;top:90px;left:0;width:100%;height:100%;background:#0000;opacity:.7}
.cart-box .cartUcihaItachi{position:fixed;z-index:-1;top:90px;left:0;width:100%;height:100%;background:#fff;cursor: default;opacity:.7}
.chatWhatsapp label input, .chatWhatsapp label select, .chatWhatsapp label textarea {position: relative;font-family: Helvetica,sans-serif;font-size:13px;z-index: 1;outline: none;border: 0;box-shadow: inset 0 -1px 0 0 rgba(0,0,0,.08);padding: 10px 0;width: 100%;background: transparent;color: #b0b0b0;text-align: left;}
.chatWhatsapp label {position: relative;display: block;width: 100%;margin: 0;padding: 0;}
#fiktur-wrapper {max-width:100%;width:1300px;margin:auto;padding:0 30px;}
.home-ad1 h3 {font-size:110%;margin:0 0 10px;font-weight:700}
.full-left {float:left;position:relative;padding:10px 0;border-top:1px dashed #ccc}
/* MEDIA RESPONSIVE QUERY */
@media only screen and (max-width:1080px){
#wrapper{margin:0 auto}
#kekiri {width:10px;}
#sidebar-wrapper {width:100%!important;max-width:100%;}
#header-wrapper,.subheader-inner{padding-left:30px;padding-right:30px;width:100%}
.link-right {padding:0;}
.input-search input{width:80%}
.search-icon{right:30px;width:40%}
.icon-tag{width:95px}}
@media only screen and (max-width:800px){
.fiktur_layanan, #admin, .subheader, .subheader-left,.toko-profil, .totalCart, #widget-footer-container, .simpleCart_items .item-price, .simpleCart_items th.item-price, .xNxx {display:none;}
.subheader {border: 0;
background:transparent!important;
z-index: 99999;
position:relative;
top: 150%;}
.subheader-inner {padding:0;display: flex;height: 0;}
.subheader .subheader-right li {float:none;line-height: 32px;
height: 32px;}
.subheader-right a {font: 500 13px Roboto, Arial, sans-serif;margin: 0;
padding: 0 17px}
.above-post-widget {display:flex}
.bus {padding:0;background-image:none}
.search-content, .cart .cart-box {position: fixed;top: 48px;left: 0;width: 100%;min-width:100%;height: calc(100% - 48px);overflow-y: auto;padding-top: 35px;background: #fff;border-radius: 0;box-shadow: none;}
#header-container {height:52px;}
#header-wrapper {padding-left:0px;padding-right:0px;position:relative;}
.post-container, #footer-wrapper {padding:0;}
.header {position: absolute;top: 0;left:-999999px;z-index:1;text-align:left;float:none;display:inline-block;}
.header img {display: inline-block !important;}
#fiktur-wrapper, .simpleCart_items {padding:0}
.icon-tag{width:80px;float:none}
.rightPage, .simpleCart_items {float:left!important;width:100%!important}
#wrapper {padding: 80px 20px 20px;}
#sidebar-wrapper {width:100%!important;max-width:100%;}
.link-right a, .menu-cart {color:#9fa6b0;height:52px;line-height:52px;top:0px!important}
a.view-cart {width:100%;float:left;height:32px!important}
.ck-btn {margin:10px 0;margin-top:20px}
#search_mini_form {margin:8px;}
#widget-footer1{width:100%;float:none;padding-right:0}
#widget-footer2{width:100%;float:none}
#widget-footer3{width:100%;float:none;padding-left:0}
#widget-footer4{width:100%;float:none;padding-left:0}
#footer-container {padding:20px 20px;}
.input-search input {width:auto!important;height:36px;line-height:20px;margin:0}
.button {left:5px;top:8px;background:0 0}
.daffadev-tutup {right:20px;}
.input-search button {border-radius: 0 8px 8px 0;background:0 0;width:24px;height:24px;padding:10px 0;}
.input-search {background:rgb(246, 246, 246);border-radius:8px;box-shadow:0 0;padding:0 30px;}
.link-right {margin:0;border:0;height:52px;line-height:52px;float:right;position:absolute;top:0;right:0;padding:0;display:flex;}
.search-icon {position:absolute;width:70%;left:40px;top:0;}
.toko-profil {float:none;padding:4px 0;height:52px;margin-right:15px;}
.full-left {border:0;float:none;padding:0 24px;width:100%;max-height:200px;overflow:hidden;font-size:0.857143rem}}
@media only screen and (max-width:480px){
.keranjang-kosong {top: 42px;width: fit-content;margin-right: 30%;}
.fa-shopping-basket {font-size: 60px!important;color: #656565;}
.tombol-beli {padding:10px 0;}
#wrapper{padding:75px 15px 15px}
#widget-footer-wrapper, #footer-wrapper {padding:0;}
.loading {text-align:left}
.input-search input{width:74%!important}
.daffadev-tutup {right:10px;}
#widget-footer-container{display:none}
.beli-lewat-chat h3{display:none}
.dot{height:7px;width:7px}
.button{top:8px;left:5px;background:0 0}
.button i {width:15px;height:15px;}
.search-icon{right:15px;width:60%}
.post-body a>img{width:100%}h1{font-size:170%}h2{font-size:150%}h3{font-size:130%}h4{font-size:120%}h5{font-size:110%}h6{font-size:100%}
h1.post-title{font-size:22px}
#searchfs input[type="search"]{font-size:26px}
#footer-wrapper .footer-right,#footer-wrapper .footer-left{float:none;margin:5px 0;text-align:center;}
.icon-tag{width:56px;margin-right:8px;min-height:58px;display:inline-block;text-align:center;position:relative;vertical-align:top;flex:1 1 0;padding:5px 0 0;text-decoration:none}
.text-label {font-size:10px}}
@media screen and (max-width:320px){.search-icon{width:50%;}}

.subheader {background:#f3f4f5;}
#post-wrapper{float:right;width:100%;max-width:1000px}
.post-container{padding:0}
.PopularPosts .product-info h2.post-title {font-size:14px;}
.PopularPosts .post:hover {box-shadow:0 0;}
#sidebar-wrapper{float:left;width:18%}
.post-outer{width:18.40%;margin-bottom:2%;margin-right:2%;float:left}
.post:hover{transition:box-shadow .25s;box-shadow:0 0 12px 0 rgba(0,0,0,0.24)}
.blog-posts .post-outer:nth-of-type(5n+0){margin-right:0}
.blog-posts .post-outer:nth-of-type(5n+1){clear:both}
.post{position:relative;box-shadow:0 1px 5px 0 rgba(0,0,0,0.1)}
.produk-info{line-height:40px;height:40px;overflow:visible}
.produk-info-container{padding: 10px 8px 0;text-align: left;overflow: hidden;}
.harga-tetap {display:block!important;overflow:unset;font-size:75%!important;line-height:10px;vertical-align:middle;}
.item_price {line-height:20px;text-align:left}
.rating-produk{display:flex;justify-content:space-between;line-height:2}
button.next1, button.prev1, .slider-image {display:none;}
.skor {color:#9e9e9e;}
.skor:after{content:""}
.gambar-produk{height:0}.gambar-produk img,.deskripsi-produk,.tombol-beli,.produk-info img,.produk-info iframe,.produk-info table, .opsi-produk, .data-produk{display:none;height:0}
.post-snippet,.rating{font-weight:normal;}
.grid-wa{display:grid;grid-gap:10px}
.grid-wa.col2{grid-template-columns:1fr 1fr}
.readmoreartikel{font-weight:normal;display:none;text-align:right}
.ini-artikel{position:absolute;top:4px;left:0;background:#333;color:#fff;padding:0 8px;font-size:12px;text-transform:uppercase;font-weight:500;height:24px;line-height:24px}
.ini-artikel:after{border-left:8px solid #333}
/* RESPONSIVE */
@media only screen and (max-width:800px){.post-outer{width:31.3%;margin-right:3%}
.blog-posts .post-outer:nth-of-type(4n+0){margin-right:3%}
.blog-posts .post-outer:nth-of-type(3n+0){margin-right:0}
.blog-posts .post-outer:nth-of-type(5n+0) {margin-right:4%}
.blog-posts .post-outer:nth-of-type(5n+1){clear:both}
.blog-posts .post-outer:nth-of-type(5n+1){clear:none}}
@media only screen and (max-width:640px){.post-outer{margin-right:4%;width:48%;margin-bottom:4%}.blog-posts .post-outer:nth-of-type(3n+0){margin-right:4%}
.blog-posts .post-outer:nth-of-type(2n+0){margin-right:0}
.blog-posts .post-outer:nth-of-type(4n+1){clear:both}
.blog-posts .post-outer:nth-of-type(4n+1){clear:none}}
@media screen and (max-width:320px){.post-outer{width:100%;margin-bottom:10px}.blog-posts .post-outer:nth-of-type(3n+0){margin-right:0}}
@import "./highlights";
@import "./svg-icons";
